* {
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f0f0f0;
  padding: 1rem;
  font-size: 1.6rem;
}
.Input {
  border-radius: 5px;
  border: 2px solid transparent;
  font-size: inherit;
  outline: none;
  padding: 10px;
  transition: border .2s;
  width: 100%;
}

.Input:focus {
  border: 2px solid #1897f9;
}
.MovieListItem {
  align-items: center;
  background: #fff;
  border-left: 5px solid #1897f9;
  border-radius: 2px;
  display: flex;
  justify-content: space-between;
  margin: 1rem 0;
  padding: 1rem;
}

.MovieListItem__Delete {
  background: none;
  border-radius: 100%;
  border: 0;
  cursor: pointer;
  height: 25px;
  outline: none;
  padding: 5px;
  width: 25px;
}

.MovieListItem__Delete img {
  width: 100%;
}

.MovieListItem__Delete:hover {
  background: #f0f0f0;
}
.MovieList {
  list-style: none;
  padding-left: 0;
}
.App {
  width: 30%;
}

.Error {
  color: red;
}
